@import './assets/variables.scss';
@import './assets/colors.scss';
@import './util/isColor.scss';
@import './util/isOutline.scss';
@import './util/isBackground.scss';

a,
button {
  min-width: 0;
  min-height: $button-height;
  line-height: $button-height;
  margin: 0 0.5rem;
  padding: 0 1rem;
  display: inline-block;
  position: relative;
  overflow: hidden;
  user-select: none;
  cursor: pointer;
  outline: none;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: $button-radius;
  transition: $swift-ease-out;
  color: $black;
  font-family: inherit;
  font-size: 13px;
  font-style: inherit;
  font-variant: inherit;
  font-weight: 500;
  letter-spacing: inherit;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  vertical-align: top;
  white-space: nowrap;
  backface-visibility: hidden;
  transform: translateZ(0) translate3d(0, 0, 0);

  &:focus {
    outline: none;
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &:hover {
    text-decoration: none;

    &:not([disabled]) {
      &:not(.is-background-green, .is-background-red, .is-background-blue, .is-background-gray, .is-background-pink, .is-background-orange, .is-background-purple, .is-background-yellow, .is-background-white, .is-background-black) {
        background-color: darken($white, 5%);
      }

      &.is-background-green,
      &.is-background-red,
      &.is-background-blue,
      &.is-background-gray,
      &.is-background-pink,
      &.is-background-orange,
      &.is-background-purple,
      &.is-background-yellow,
      &.is-background-white,
      &.is-background-black {
        opacity: 0.8;
      }
    }
  }

  &:active:not([disabled]) {
    transform: scale(1.1);
  }

  &.is-shadow:not([disabled]) {
    box-shadow: $material-shadow-2dp;
  }

  &.md-icon-button,
  &.md-fab {
    .md-icon {
      position: absolute;
      top: 1px;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  &.md-icon-button {
    width: $button-height;
    min-width: $button-height;
    height: $button-height;
    min-height: $button-height;
    line-height: $button-height;
    margin: 0 6px;
    padding: 4px;
    border-radius: 50%;

    &:not([disabled]) {
      &:hover {
        background: none;
      }
    }
  }

  &.md-fab {
    width: $button-fab-size;
    height: $button-fab-size;
    padding: 0;
    min-width: 0;
    overflow: hidden;
    box-shadow: $material-shadow-2dp;
    border-radius: $button-fab-size;
    line-height: $button-fab-size;
    background-clip: padding-box;
    transition: $swift-ease-out;
    transition-property: background-color, box-shadow, transform;

    &:hover,
    &:focus {
      box-shadow: $material-shadow-5dp;
    }

    &.md-mini {
      width: $button-fab-size-mini;
      height: $button-fab-size-mini;
      line-height: $button-fab-size-mini;
    }
  }

  &[disabled] {
    color: rgba($black, 0.26);
    cursor: default;
    pointer-events: none;

    &.is-shadow,
    &.md-fab {
      background-color: rgba($black, 0.12);
    }

    &.md-fab {
      box-shadow: none;
    }
  }

  &:after {
    transition: $swift-ease-out;
  }
}

// .md-button.md-icon-button,
// .md-button.md-fab {
//   .md-icon {
//     color: inherit;
//     display: block;
//   }
// }

.md-fab,
.md-speed-dial {
  &.md-fab-top-left,
  &.md-fab-top-center,
  &.md-fab-top-right,
  &.md-fab-bottom-left,
  &.md-fab-bottom-center,
  &.md-fab-bottom-right {
    margin: 0;
    position: absolute;
    z-index: 10;
  }

  &.md-fab-top-left {
    top: $button-fab-space;
    left: $button-fab-space;
  }

  &.md-fab-top-center {
    top: $button-fab-space;
    left: 50%;
    transform: translateX(-50%);
  }

  &.md-fab-top-right {
    top: $button-fab-space;
    right: $button-fab-space;
  }

  &.md-fab-bottom-left {
    bottom: $button-fab-space;
    left: $button-fab-space;
  }

  &.md-fab-bottom-center {
    bottom: $button-fab-space;
    left: 50%;
    transform: translateX(-50%);
  }

  &.md-fab-bottom-right {
    right: $button-fab-space;
    bottom: $button-fab-space;
  }
}

.md-button-tooltip {
  &.md-tooltip-top {
    margin-top: -8px;
  }

  &.md-tooltip-right {
    margin-left: 8px;
  }

  &.md-tooltip-bottom {
    margin-top: 8px;
  }

  &.md-tooltip-left {
    margin-left: -8px;
  }
}
